umn;\n}\n.flex-row {\n  display: flex;\n  flex-direction: row;\n}\n.flex-stretch {\n  flex-grow: 1;\n}\n.flex-fixed,\n.flex-divider {\n  flex-shrink: 0;\n}\n.flex-center-children,\n.ui-overlay {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n.flex-divider {\n  width: 2px;\n  height: 2px;\n}\n.ui-fullscreen,\n.ui-overlay,\n.ui-overlay-shade {\n  position: absolute;\n  left: 0;\n  top: 0;\n  right: 0;\n  bottom: 0;\n}\n.ui-scroll,\n.ui-select {\n  overflow-y: auto;\n}\n.ui-no-select {\n  user-select: none;\n}\n.ui-text,\n:root {\n  font: 20px 'Roboto', sans-serif;\n  color: #dee7ed;\n}\n.ui-header,\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  font-family: 'Roboto Condensed', sans-serif;\n  opacity: 0.9;\n  font-weight: 300;\n  padding: 0;\n  margin: 0 0 1rem;\n}\n.ui-color-main,\n.ui-overlay-panel,\n.ui-overlay-panel-left,\n.ui-overlay-panel-right {\n  background-color: #264c73;\n}\n.ui-color-dark,\n.ui-input,\n.ui-button,\n.ui-select,\n.ui-dropdown {\n  background-color: #1d3a58;\n}\n.ui-color-darker,\n.ui-dropdown .ui-dropdown-items {\n  background-color: #172e46;\n}\n.ui-color-light {\n  background-color: #3870a8;\n}\n.ui-color-lighter {\n  background-color: #538dc6;\n}\n.ui-highlight-red {\n  background-color: rgba(231,76,60,0.2);\n  color: #ec7063;\n}\n.ui-highlight-green,\n.ui-select .ui-select-active,\n.selection .active {\n  background-color: rgba(39,174,96,0.2);\n  color: #3dd37c;\n}\n.ui-highlight-blue {\n  background-color: rgba(52,152,219,0.2);\n  color: #5dade2;\n}\n.ui-border,\n.ui-link,\n.ui-input,\n.ui-select,\n.ui-dropdown {\n  border: none;\n  border-bottom: 3px solid #3870a8;\n}\n.ui-link {\n  color: inherit;\n  text-decoration: none;\n}\n.ui-field {\n  margin-bottom: 1em;\n}\n.ui-input,\n.ui-button,\n.ui-select,\n.ui-dropdown {\n  color: inherit;\n  font-family: inherit;\n  font-size: 0.9em;\n  padding: 0.4em 0.75em;\n}\n.ui-input,\n.ui-select,\n.ui-dropdown {\n  width: 100%;\n}\n.ui-button {\n  border: none;\n  padding-left: 1.5em;\n  padding-right: 1.5em;\n}\n.ui-overlay-shade {\n  background-color: rgba(0,0,0,0.3);\n}\n.ui-overlay-panel,\n.ui-overlay-panel-left,\n.ui-overlay-panel-right {\n  box-shadow: 0px 4px 12px rgba(0,0,0,0.5);\n  width: min-content;\n}\n.ui-overlay-panel-left {\n  position: fixed;\n  top: 0;\n  bottom: 0;\n  left: 0;\n}\n.ui-overlay-panel-right {\n  position: fixed;\n  top: 0;\n  bottom: 0;\n  right: 0;\n}\n.ui-icon {\n  height: 1.1em;\n  width: 1.1em;\n}\n.ui-input-icon {\n  position: relative;\n}\n.ui-input-icon .ui-icon {\n  position: absolute;\n  left: 0.4em;\n  top: 0.4em;\n  opacity: 0.5;\n  pointer-events: none;\n}\n.ui-input-icon .ui-input {\n  padding-left: 2.1em;\n}\n.ui-select {\n  width: 14em;\n  height: 18em;\n  padding: 0;\n}\n.ui-select * {\n  display: block;\n  padding: 0.5em;\n}\n.ui-dropdown {\n  padding: 0 !important;\n  position: relative;\n}\n.ui-dropdown .ui-icon {\n  float: right;\n  position: relative;\n  top: 0.2em;\n  opacity: 0.5;\n}\n.ui-dropdown .ui-dropdown-head {\n  display: block;\n  padding: 0.5em 0.6em;\n}\n.ui-dropdown .ui-dropdown-items {\n  z-index: 420;\n}\n.ui-dropdown .ui-dropdown-items a {\n  display: block;\n  padding: 0.5em 0.6em;\n}\n* {\n  box-sizing: border-box;\n}\ndiv {\n  padding: 0;\n  margin: 0;\n}\na {\n  color: inherit;\n  text-decoration: none;\n}\n::-webkit-scrollbar {\n  width: 8px;\n  height: 8px;\n}\n::-webkit-scrollbar-track,\n::-webkit-scrollbar-corner {\n  background-color: #172e46;\n}\n::-webkit-scrollbar-thumb {\n  background-color: #3870a8;\n}\n::-webkit-scrollbar-button {\n  display: none;\n}\n::selection {\n  background: rgba(0,0,0,0.3);\n}\n.selection {\n  width: 14em;\n  height: 18em;\n}\n.selection * {\n  display: block;\n  padding: 0.5em;\n}\n","@import 'flex'\n\n// color definitions\n$colors = {\n  text: hsl(205, 30%, 90%)\n\n  main: hsl(210, 50%, 30%)\n  dark: hsl(211, 50%, 23%)\n  light: lighten(hsl(210, 50%, 30%), 20%)\n\n  red: rgb(231, 76, 60)\n  blue: rgb(52, 152, 219)\n  green: rgb(39, 174, 96)\n}\n\n// helpers\n.ui-fullscreen\n  position: absolute\n  left: 0\n  top: 0\n  right: 0\n  bottom: 0\n\n.ui-scroll\n  overflow-y: auto\n\n.ui-no-select\n  user-select: none\n\n// color and highlight styles\n.ui-text\n  font: 20px 'Roboto', sans-serif\n  color: $colors.text\n\n.ui-header\n  font-family: 'Roboto Condensed', sans-serif\n  opacity: 0.9\n  font-weight: 300\n  padding: 0\n  margin: 0 0 1rem\n\n.ui-color-main { background-color: $colors.main }\n.ui-color-dark { background-color: $colors.dark }\n.ui-color-darker { background-color: darken($colors.dark, 20%) }\n.ui-color-light { background-color: $colors.light }\n.ui-color-lighter { background-color: lighten($colors.light, 20%) }\n\nfor $color-name in ('red' 'green' 'blue')\n  $color = $colors[$color-name]\n  .ui-highlight-{$color-name}\n    background-color: rgba($color, 0.2)\n    color: lighten($color, 20%)\n\n// complimentary / additive composition styling\n.ui-border\n  border: none\n  border-bottom: 3px solid $colors.light\n\n// elements / components\n.ui-link\n  @extend .ui-border\n  color: inherit\n  text-decoration: none\n\n.ui-field\n  margin-bottom: 1em\n\n.ui-input, .ui-button\n  @extend .ui-color-dark\n  color: inherit\n  font-family: inherit\n  font-size: 0.9em\n  padding: 0.4em 0.75em\n\n.ui-input\n  @extend .ui-border\n  width: 100%\n\n.ui-button\n  border: none\n  padding-left: 1.5em\n  padding-right: 1.5em\n\n.ui-overlay\n  @extend .ui-fullscreen, .flex-center-children\n\n.ui-overlay-shade\n  @extend .ui-fullscreen\n  background-color: rgba(black, 0.3)\n\n.ui-overlay-panel\n  @extend .ui-color-main\n  box-shadow: 0px 4px 12px rgba(black, 0.5)\n  width: min-content\n\nfor $side in ('left' 'right')\n  .ui-overlay-panel-{$side}\n    @extend .ui-overlay-panel\n    position: fixed\n    top: 0\n    bottom: 0\n    {$side}: 0\n\n// fixed size on icons\n.ui-icon\n  height: 1.1em\n  width: 1.1em\n\n.ui-input-icon\n  position: relative\n\n  .ui-icon\n    position: absolute\n    left: 0.4em\n    top: 0.4em\n    opacity: 0.5\n    pointer-events: none\n\n  .ui-input\n    padding-left: 2.1em\n\n.ui-select\n  @extend .ui-input, .ui-scroll\n  width: 14em\n  height: 18em\n  padding: 0\n\n  *\n    display: block\n    padding: 0.5em\n\n  .ui-select-active\n    @extend .ui-highlight-green\n\n.ui-dropdown\n  @extend .ui-input\n  padding: 0 !important\n  position: relative\n\n  .ui-icon\n    float: right\n    position: relative\n    top: 0.2em\n    opacity: 0.5\n\n  .ui-dropdown-head\n    display: block\n    padding: 0.5em 0.6em\n\n  .ui-dropdown-items\n    @extend .ui-color-darker\n    z-index: 420\n\n    a\n      display: block\n      padding: 0.5em 0.6em\n\n// root element styles\n*\n  box-sizing: border-box\n\ndiv\n  padding: 0\n  margin: 0\n\n:root\n  @extend .ui-text\n\nh1, h2, h3, h4, h5, h6\n  @extend .ui-header\n\na\n  color: inherit\n  text-decoration: none\n\n::-webkit-scrollbar\n  width: 8px\n  height: 8px\n\n::-webkit-scrollbar-track, ::-webkit-scrollbar-corner\n  background-color: darken($colors.dark, 20%)\n\n::-webkit-scrollbar-thumb\n  background-color: $colors.light\n\n::-webkit-scrollbar-button\n  display: none\n\n::selection\n  background: rgba(black, 0.3)\n","\r\n@import '../styles/ui.styl'\r\n\r\n.selection\r\n  width: 14em\r\n  height: 18em\r\n\r\n.selection *\r\n  display: block\r\n  padding: 0.5em\r\n\r\n.selection .active\r\n  @extend .ui-highlight-green\r\n"],"sourceRoot":"webpack://"}]);

